@import url(https://fonts.googleapis.com/css2?family=Comfortaa:wght@300;600&display=swap);
body {
  font-family: 'Comfortaa';
  font-weight: 600;
  background-color: #ecf0f1;
}

input {
  font-weight: bolder;
  margin-bottom: 10px;
}

.form-title {
  width: 150px;
  text-align: right;
}

.switcher {
  position: absolute;
  top: 55px;
  right: 10px;
  z-index: 2;
}

.inputwrapper {
  margin-bottom: 10px;
}

.ant-menu {
  text-align: center;
  border: none;
}

.ant-menu .ant-menu-item-selected,
.ant-menu .ant-menu-item-active {
  border-color: rebeccapurple !important;
}

.ant-menu .ant-menu-item-selected a,
.ant-menu .ant-menu-item-active a {
  color: rebeccapurple !important;
}

.dark {
  min-height: 100vh;
  background-color: #2d3436;
  color: #ecf0f1;
}

.dark h1, .dark h2, .dark h3 {
  color: #ecf0f1;
}

.dark .MuiFormControl-root {
  border-color: yellowgreen;
}

.dark .MuiFormControl-root label {
  color: white;
}

.dark input {
  background-color: #2d3436;
  color: white;
}

.dark .ant-menu {
  background-color: #2d3436;
}

.dark .ant-menu .ant-menu-item a {
  color: #b2bec3;
}

.dark .ant-menu .ant-menu-item-selected,
.dark .ant-menu .ant-menu-item-active {
  border-color: white !important;
}

.dark .ant-menu .ant-menu-item-selected a,
.dark .ant-menu .ant-menu-item-active a {
  color: white !important;
}

.dark .ant-table .ant-table-content table {
  background-color: #2d3436;
  color: white;
}

.dark .ant-table .ant-table-content table thead tr th {
  background-color: #2d3436;
  color: white;
  font-size: 120%;
  font-weight: bolder;
}

.dark .ant-table .ant-table-content table tbody tr td {
  border: none;
}

.dark .ant-table .ant-table-content table tbody tr:hover {
  color: #3498db;
}

